  display: none;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 5rem;
  margin-bottom: 5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.btn {
  border: none;
  background-color: #ff4800;
  padding: 1.5rem 1.2rem;
  color: #fff;
  font-weight: 700;
  font-size: 1.8rem;
  border-radius: 1rem;
  width: fit-content;
}
.btn:hover {
  background-color: #ffba00;
  color: #000;
}

/** UI **/
.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
  /* aqui cuanod las clases estan uno frente al otro, entonces las clases deben ir con punto juntos (.header.incio) */
  /* para lograr eso ponemos un &.class dentro de la clse que esta primero en el html, asi estaran (.header.inicio) */
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}

.logoptipo {
  width: 20rem;
  height: auto;
}

/** Brgin::mobile menu **/
.mobile-menu img {
  width: 4rem;
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu:hover {
  cursor: pointer;
}

/** End::mobile menu **/
.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;
  row-gap: 3rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.1s ease-in-out, opacity 0.3s linear;
  height: 0;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    visibility: visible;
    opacity: 1;
  }
}
.navegacion a {
  color: #fff;
  text-align: center;
  display: block;
  font-size: 2.2rem;
  text-decoration: none;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
  }
  .navegacion a:last-of-type {
    margin: 0;
  }
}
.navegacion a:hover {
  color: #ffba00;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.eslogan {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
@media (min-width: 768px) {
  .eslogan {
    height: 55rem;
  }
}
.eslogan h1 {
  text-align: center;
  color: #fff;
  font-weight: 700;
  line-height: 2;
  margin: 0 0 2rem 0;
}
@media (min-width: 768px) {
  .eslogan h1 {
    margin: 2.5rem 0;
    max-width: 50rem;
    text-align: left;
  }
}
.eslogan h2 {
  text-align: center;
  color: #fff;
  font-weight: 400;
  margin: 0 0 2rem 0;
}
@media (min-width: 768px) {
  .eslogan h2 {
    max-width: 60rem;
    margin: 2.5rem 0;
    text-align: left;
  }
}
.eslogan p {
  color: #fff;
  font-size: 2.2rem;
}
@media (min-width: 768px) {
  .eslogan p {
    max-width: 60rem;
    margin: 2.5rem 0;
    text-align: left;
  }
}
.eslogan .btn {
  margin: 2.5rem 0;
  border: none;
  background-color: #ff4800;
  padding: 1.5rem 1.2rem;
  color: #fff;
  font-weight: 700;
  font-size: 1.8rem;
  border-radius: 1rem;
  width: auto;
  text-align: center;
}
.eslogan .btn:hover {
  background-color: #ffba00;
  color: #000;
}
@media (min-width: 768px) {
  .eslogan .btn {
    border: none;
    background-color: #ff4800;
    padding: 1.5rem 1.2rem;
    color: #fff;
    font-weight: 700;
    font-size: 1.8rem;
    border-radius: 1rem;
    width: fit-content;
  }
  .eslogan .btn:hover {
    background-color: #ffba00;
    color: #000;
  }
}

.titulo-seccion {
  margin-top: 5rem;
  font-size: 4rem;
  font-weight: 700;
}

.contenedor-servicios {
  padding: 2rem;
}
@media (min-width: 768px) {
  .contenedor-servicios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.contenido-servicio {
  text-align: center;
  border: 1px solid #999999;
  margin-bottom: 4rem;
  border-radius: 2rem;
  padding: 3rem 2rem;
}
@media (min-width: 768px) {
  .contenido-servicio {
    margin-bottom: 0;
  }
}
.contenido-servicio h2 {
  font-weight: 900;
  margin-bottom: 1rem;
  margin: 0 0 2rem 0;
}
.contenido-servicio .icono-caracteristicas {
  display: flex;
  justify-content: space-evenly;
  list-style: none;
  margin: 3rem 0;
  padding: 0;
}
.contenido-servicio .icono-caracteristicas img {
  max-width: 5rem;
}
.contenido-servicio .icono-caracteristicas a {
  align-items: center;
}
.contenido-servicio h3 {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 3rem;
  font-weight: 400;
  line-height: 1.5;
}
@media (min-width: 768px) {
  .contenido-servicio h3 {
    height: 16rem;
    margin: 0;
  }
}
.contenido-servicio .texto-truncado {
  margin-top: 0;
  text-align: center;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  /* Mostrar solo 3 líneas */
  -webkit-box-orient: vertical;
  overflow: hidden;
  white-space: normal;
  /* Para que se muestre con múltiples líneas truncadas */
}

.clientes {
  text-align: center;
}
.clientes h3 {
  font-weight: 700;
  line-height: 1.5;
  margin: 0;
}
.clientes h4 {
  font-weight: 400;
}
.clientes a {
  margin: 4rem 0;
  display: block;
}
@media (min-width: 768px) {
  .clientes {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(3, 1fr);
  }
}
.clientes .cliente {
  padding: 2rem;
}
@media (min-width: 768px) {
  .clientes .cliente .cliente-header {
    height: 26%;
    display: inline-flex;
    align-items: center;
  }
}
.clientes .cliente:last-of-type {
  margin-bottom: 4rem;
}
.clientes .cliente:hover {
  border-bottom: 1px solid #ff4800;
}

.footer {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .footer {
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
  }
}

.footer__logo {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
@media (min-width: 768px) {
  .footer__logo {
    flex-direction: row;
    column-gap: 2rem;
    text-align: left;
  }
}

.footer__iconos {
  display: flex;
  justify-content: space-around;
}
@media (min-width: 768px) {
  .footer__iconos {
    column-gap: 4rem;
  }
}
.footer__iconos img {
  max-width: 5rem;
  height: auto;
}